<template>
  <view class="form-container">
    <header class="form-header">
      <h1 class="title">✨ 发布导游服务</h1>
      <button class="close-btn" @click="navigateBack">×</button>
    </header>

    <scroll-view class="form-content" scroll-y>
      <form @submit.prevent="submitForm">
        <view class="form-section">
          <label class="form-label">服务类型</label>
          <view class="type-grid">
            <button
              v-for="type in serviceTypeOptions"
              :key="type"
              :class="['type-btn', { active: form.type === type }]"
              @click.prevent="form.type = type"
            >
              {{ type }}
            </button>
          </view>
        </view>

        <!-- 添加更多字段 -->
        <view class="form-section">
          <label class="form-label">服务价格（元/天）</label>
          <input
            v-model.number="form.price"
            type="number"
            class="form-input"
            placeholder="请输入收费标准"
          />
        </view>

        <view class="form-section">
          <label class="form-label">服务描述</label>
          <textarea
            v-model="form.description"
            class="form-textarea"
            placeholder="详细描述您的服务内容..."
          />
        </view>

        <button class="submit-btn" form-type="submit">立即发布</button>
      </form>
    </scroll-view>

    <loading-overlay :visible="isSubmitting" />
  </view>
</template>

<script setup>
import { ref } from 'vue';

const serviceTypeOptions = ['景点讲解', '行程规划', '全程陪同', '摄影向导'];

const form = ref({
  type: '',
  price: null,
  description: '',
  // 其他字段...
});

const submitForm = async () => {
  try {
    await http.post('/guide-services', form.value);
    uni.navigateBack();
  } catch (error) {
    uni.showToast({ title: '提交失败', icon: 'error' });
  }
};
</script>

